import http from '../http-common-token';

const getAll = () => {
    return http.get('blogs'); 
}

const getById = (id) => {
    return http.get(`blogs/${id}`);
}

const create = (data) => {
    return http.post('blogs' , data);
}
const update = (id, data) => {
    return http.put(`blogs/${id}`, data);
}
const remove = (id) => {
    return http.delete(`blogs/${id}`);
}

export default
{
  
    getAll,
    create,
    getById,
    update,
    remove
}
